/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import callApi from "../utils/apiCaller";
import { Button, Card, Divider } from "antd";
import { formatNumber } from "../config/TYPE";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
const MySwal = withReactContent(Swal);

const StatusOrders = {
  Unconfirm: {
    text: "Cha xc nhn",
    color: "gray",
  },
  Confirm: {
    text: " xc nhn",
    color: "blue",
  },
  Shipping: {
    text: "ang giao hng",
    color: "yellow",
  },
  Complete: {
    text: "Hon thnh",
    color: "green",
  },
  Canceled: {
    text: "Hy b",
    color: "red",
  },
};

const PaymentMethods = {
  COD: "Thanh ton khi nhn hng",
  VNPAY: "Thanh ton qua VNPay",
};

function OrderHistoryPage({ match }) {
  const { id } = match.match.params;
  console.log("object", id);

  const [data, setData] = useState(null);
  const [dataOrderDetail, setDataOrderDetail] = useState(null);
  const dataOrderDetails = dataOrderDetail || data?.orderDetails;
  const [loading, setLoading] = useState(true);
  console.log("===data", data);

  const getData = async () => {
    const token = localStorage.getItem("_auth");
    if (token) {
      const res = await callApi(`orders/${id}`, "GET", null, token);
      const res2 = await callApi(
        `order/${id}/orderDetails`,
        "GET",
        null,
        token
      );
      setData(res.data);
      setDataOrderDetail(res2.data?.results);
      setLoading(false);
    }
  };

  useEffect(() => {
    getData();
  }, [id]);

  const handleCancelOrder = () => {
    const token = localStorage.getItem("_auth");
    if (!token) return;
    
    MySwal.fire({
      title: "Bn chc chn mun hu?",
      text: "Bn khng th khi phc",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "ng ",
      cancelButtonText: "Hu",
    }).then(async (result) => {
      if (result.value) {
        const res = await callApi(
          `users/order/${id}/cancel`,
          "PUT",
          null,
          token
        );
        if (res && res.status === 200) {
          Swal.fire(
            "Hu thnh cng!",
            "n hng ca bn  c hu",
            "success"
          );
          getData();
        }
      }
    });
  }

  const getAddress = (data) => {
    if (!data) return "";
    return `${data.address.house}, ${data.address.state}, ${data.address.province}`;
  };

  return (
    <div>
      <div className="container OrderHistoryPage">
        <h3>Chi tit n hng</h3>
        <Divider />
        {loading && <div>ang ti...</div>}
        {data && (
          <>
            <div className="line">
              <p className="order-title-row">M n hng: </p>
              <p className="order-line-value">{data?.id}</p>
            </div>
            <div className="line">
              <p className="order-title-row">Tn ngi nhn: </p>
              <p className="order-line-value">{data?.fullName}</p>
            </div>
            <div className="line">
              <p className="order-title-row">a ch: </p>
              <p className="order-line-value">{getAddress(data)}</p>
            </div>
            {data.note && (
              <div className="line">
                <p className="order-title-row">Ghi ch: </p>
                <p className="order-line-value">{data.note}</p>
              </div>
            )}

            <div className="line">
              <p className="order-title-row">Sn phm: </p>
              <div style={{ marginLeft: 40, flex: 1 }}>
                <table className="table table-hover">
                  <thead>
                    <tr>
                      <th>STT</th>
                      <th>Tn sn phm</th>
                      <th>nh</th>
                      <th>S lng</th>
                      <th>Gi</th>
                    </tr>
                  </thead>
                  <tbody>
                    {dataOrderDetails && dataOrderDetails.length
                      ? dataOrderDetails.map((item, index) => {
                          return (
                            <tr key={index}>
                              <th scope="row">{index + 1}</th>
                              <td>{item.nameProduct}</td>
                              <td>
                                <div
                                  style={{
                                    width: 60,
                                    overflow: "hidden",
                                  }}
                                >
                                  <img
                                    src={
                                      item.product
                                        ? item.product?.image
                                        : item.imageProduct
                                    }
                                    alt="not found"
                                    style={{
                                      height: "100%",
                                      width: "100%",
                                      objectFit: "contain",
                                    }}
                                  />
                                </div>
                              </td>
                              <td>{item.quantity || 0}</td>

                              <td>
                                {formatNumber.format(
                                  item.quantity * item.price
                                )}
                              </td>
                            </tr>
                          );
                        })
                      : null}
                  </tbody>
                </table>
              </div>
            </div>
            <div className="line">
              <div className="line">
                <p className="order-title-row">Tng tin sn phm: </p>
                <p className="order-line-value">
                  {formatNumber.format(data?.itemAmount || 0)}
                </p>
              </div>
              <div className="line" style={{ marginLeft: 40 }}>
                <p className="order-title-row">Ph vn chuyn: </p>
                <p className="order-line-value">
                  {formatNumber.format(data?.shippingTotal || 0)}
                </p>
              </div>
            </div>
            <div className="line">
              <div className="line">
                <p className="order-title-row">Tng gim gi: </p>
                <p className="order-line-value">
                  {formatNumber.format(data?.promoTotal || 0)}
                </p>
              </div>
              <div className="line" style={{ marginLeft: 40 }}>
                <p className="order-title-row">Tng tin: </p>
                <p className="order-line-value">
                  {formatNumber.format(data?.totalAmount || 0)}
                </p>
              </div>
            </div>
            <div className="line">
              <p className="order-title-row">Trng thi: </p>
              <p
                className="order-line-value"
                style={{
                  color: data?.status ? StatusOrders[data.status].color : "",
                  backgroundColor: "#74dbef",
                  padding: "0px 5px",
                }}
              >
                {data?.status ? StatusOrders[data.status].text : ""}
              </p>
            </div>
            <div className="line">
              <p className="order-title-row">Phng thc thanh ton: </p>
              <p className="order-line-value">
                {data?.paymentMethod ? PaymentMethods[data.paymentMethod] : ""}
              </p>
            </div>
            <div className="line">
              <img
                src={`/images/${data?.isPaid ? "ic-paid" : "ic-unpaid"}.png`}
                alt="ic-paid"
                style={{ width: 150 }}
              />
            </div>
            <div className="line" style={{ justifyContent: "center" }}>
              <Button
                type="primary"
                danger
                disabled={data.status !== "Unconfirm"}
                onClick={handleCancelOrder}
              >
                Hy n hng
              </Button>
            </div>
          </>
        )}
      </div>
    </div>
  );
}

export default OrderHistoryPage;
