import React, { Component } from 'react'
import { connect } from 'react-redux'
import { formatNumber } from '../../config/TYPE'
import { Redirect } from 'react-router-dom'
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
toast.configure()

class SumTotal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      redirectYourOrder: false,
      redirectYourLogin: false
    }
  }


  checkAuthenticate = () => {
    const { user, cartCalculateItems } = this.props;
    if (!cartCalculateItems.itemAmount) {
      return toast.error('Xin vui lng chn sn phm trc khi t hng');
    }
    if (user) {
      this.setState({
        redirectYourOrder: true
      })
    } else {
      toast.error('Bn phi ng nhp trc khi thanh ton');
      this.setState({
        redirectYourLogin: true
      })
    }
  }

  render() {
    const { redirectYourOrder, redirectYourLogin } = this.state;
    const { cartCalculateItems } = this.props;
    if (redirectYourOrder) {
      return <Redirect to="/checkout"></Redirect>
    }
    if (redirectYourLogin) {
      return <Redirect to="/login-register"></Redirect>
    }
    return (
      <div>
        <div className="cart-page-total">
          <h2>Tng tin gi hng</h2>
          <ul>
            <li>Tng tin <span>{cartCalculateItems.itemAmount ? formatNumber.format(cartCalculateItems.itemAmount) : 0}</span></li>
            <li>Ph vn chuyn <span>{formatNumber.format(cartCalculateItems.shippingTotal || 0)}</span></li>
            <li>Gim gi  <span>{formatNumber.format(cartCalculateItems.promoTotal || 0)}</span></li>
            <li style={{ color: 'red' }}>Tng <span>{cartCalculateItems.totalAmount ? formatNumber.format(cartCalculateItems.totalAmount) : 0}</span></li>
          </ul>
          <button onClick={() => this.checkAuthenticate()} className="fix-text-checkout" style={{cursor: 'pointer'}}>t hng</button>
        </div>
      </div>
    )
  }
}


const mapStateToProps = (state) => {
  return {
    user: state.auth,
    cartCalculateItems: state.cartCalculateItems.data
  }
}
export default connect(mapStateToProps, null)(SumTotal)
