import React, { Component } from 'react'
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { actRegisterRequest } from '../../redux/actions/auth';
import { connect } from 'react-redux'
import { startLoading, doneLoading } from '../../utils/loading'
import { Link } from 'react-router-dom/cjs/react-router-dom.min';
toast.configure()

class Register extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      email: '',
      password: '',
      repassword: ''
    }
  }

  handleChange = (event) => {
    let name = event.target.name;
    let value = event.target.type === 'checkbox' ? event.target.checked : event.target.value;
    this.setState({
      [name]: value
    });
  }

  handleSubmit = async (event) => {
    event.preventDefault();
    const { name, email, password, repassword } = this.state;
    if (password !== repassword){
      toast.error('Mt khu khng ging nhau')
      return 
    }
    if (password.length < 6 || password.length > 32) {
      toast.error('Mt khu phi t 6 n 32 k t');
      return
    }
    const newName = (name !== null)  ? name : ''
    const user = {
      name: newName,
      email,
      password,
    }
    startLoading();
    await this.props.registerRequest(user);
    this.setState({
      name: '',
      email: '',
      password: '',
      repassword: ''
    })
    
    doneLoading();
  }

  render() {
    const { name, email } = this.state;
    return (
      <div className="col-sm-12 col-md-12 col-lg-6 col-xs-12">
        <form onSubmit={(event) => this.handleSubmit(event)}>
          <div className="login-form">
            <h4 className="login-title">ng k</h4>
            <div className="row">
              <div className="col-md-12 mb-20">
                <label>H tn</label>
                <input onChange={this.handleChange} value={name} className="mb-0" type="text" name="name" placeholder="Nhp tn ca bn" />
              </div>
              <div className="col-md-12 mb-20">
                <label>Email*</label>
                <input onChange={this.handleChange} value={email} className="mb-0" type="email" name="email" placeholder="Nhp a ch email ca bn" />
              </div>
              <div className="col-md-6 mb-20">
                <label>Mt khu*</label>
                <input onChange={this.handleChange} className="mb-0" type="password" name="password" placeholder="Nhp mt khu" />
              </div>
              <div className="col-md-6 mb-20">
                <label>Xc nhn mt khu*</label>
                <input onChange={this.handleChange} className="mb-0" type="password" name="repassword" placeholder="Nhp li mt khu" />
              </div>
              <div className="col-12 row justify-content-center">
                <button className="register-button mt-0">ng k</button>
              </div>
              <div className='col-12 row justify-content-center mt-10'>
                <p>Bn cha c ti khon? <Link to="/login-register">ng nhp</Link></p>
              </div>
            </div>
          </div>
        </form>
      </div>
    )
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    registerRequest: (user) => {
      dispatch(actRegisterRequest(user))
    }
  }
}

export default connect(null, mapDispatchToProps)(Register)

